// https://www.cnblogs.com/blqw/p/3782420.html
// http://www.smartjava.org/content/record-audio-using-webrtc-chrome-and-speech-recognition-websockets
// http://www.it1352.com/305552.html
// 4410016000(resample audio buffer from 44100 to 16000)
navigator.getUserMedia = ( navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia || navigator.mediaDevices.getUserMedia)
function HZRecorder () {
  // 
  window.URL = window.URL || window.webkitURL
  navigator.getUserMedia = ( navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia || navigator.mediaDevices.getUserMedia)
}
function HZRecorderInit (stream, config) {
  // var canRecording = navigator.getUserMedia != null
  config = config || {}
  config.sampleBits = config.sampleBits || 16 //  8, 16
  config.sampleRate = config.sampleRate || 16000 // 16khz
  // 44100)
  var context = new (window.webkitAudioContext || window.AudioContext)()
  var audioInput = context.createMediaStreamSource(stream)
  var createScript =
    context.createScriptProcessor || context.createJavaScriptNode
  var recorder = createScript.apply(context, [4096, 1, 1])
  var audioData = {
    data:null,
    size: 0, // 
    buffer: [], // 
    inputSampleRate: context.sampleRate, //  48000
    inputSampleBits: 16, //  8, 16
    outputSampleRate: config.sampleRate, // 
    oututSampleBits: config.sampleBits, //  8, 16
    input: function (data) {
      this.buffer.push(new Float32Array(data))
      this.size += data.length
    },
    getbase64(callback){
        var reader = new FileReader();
        reader.readAsDataURL(this.encodeWAV(this.buffer)); //  base64
        reader.onloadend = function () {
            var audioBase64 = reader.result;//base64
            audioBase64 = audioBase64.replace(/^data:audio\/\w+;base64,/, "")
            callback( audioBase64)
        };
    },
    compress: function () {
      // 
      // 
      var data = new Float32Array(this.size)
      var offset = 0
      for (var i = 0; i < this.buffer.length; i++) {
        data.set(this.buffer[i], offset)
        offset += this.buffer[i].length
      }
      // 
      var compression = parseInt(this.inputSampleRate / this.outputSampleRate)
      var length = data.length / compression
      var result = new Float32Array(length)
      var index = 0
      var j = 0
      while (index < length) {
        result[index] = data[j]
        j += compression
        index++
      }
      return result
    },
    clear: function () {
      this.size = 0
      this.buffer = []
    },
    encodeWAVNoHead: function () {
      var sampleBits = Math.min(this.inputSampleBits, this.oututSampleBits)
      var bytes = this.compress()
      var dataLength = bytes.length * (sampleBits / 8)
      var buffer = new ArrayBuffer(44 + dataLength)
      var data = new DataView(buffer)
      var offset = 0
      // 
      if (sampleBits === 8) {
        for (var i = 0; i < bytes.length; i++, offset++) {
          var s = Math.max(-1, Math.min(1, bytes[i]))
          var val = s < 0 ? s * 0x8000 : s * 0x7fff
          val = parseInt(255 / (65535 / (val + 32768)))
          data.setInt8(offset, val, true)
        }
      } else {
        for (var index = 0; index < bytes.length; index++, offset += 2) {
          var sIndex = Math.max(-1, Math.min(1, bytes[index]))
          data.setInt16(
            offset,
            sIndex < 0 ? sIndex * 0x8000 : sIndex * 0x7fff,
            true
          )
        }
      }
      return new Blob([data], { type: 'audio/wav' })
    },
    encodeWAV: function () {
      var sampleRate = Math.min(this.inputSampleRate, this.outputSampleRate);
      var sampleBits = Math.min(this.inputSampleBits, this.oututSampleBits);
      var bytes = this.compress();
      var dataLength = bytes.length * (sampleBits / 8);
       var buffer = new ArrayBuffer(44 + dataLength);
      // var buffer = new ArrayBuffer(dataLength);
      var data = new DataView(buffer);
    
      var channelCount = 1;// 
      var offset = 0;
      var writeString = function (str) {
        for (var i = 0; i < str.length; i++) {
          data.setUint8(offset + i, str.charCodeAt(i));
        }
      }
       //  
       writeString('RIFF'); offset += 4;
       // ,-8 
       data.setUint32(offset, 36 + dataLength, true); offset += 4;
       // WAV
       writeString('WAVE'); offset += 4;
       //  
       writeString('fmt '); offset += 4;
       // , 0x10 = 16 
       data.setUint32(offset, 16, true); offset += 4;
       //  (PCM) 
       data.setUint16(offset, 1, true); offset += 2;
       //  
       data.setUint16(offset, channelCount, true); offset += 2;
       // ,, 
       data.setUint32(offset, sampleRate, true); offset += 4;
       //  () /8 
       data.setUint32(offset, channelCount * sampleRate * (sampleBits / 8), true); offset += 4;
       //   /8 
       data.setUint16(offset, channelCount * (sampleBits / 8), true); offset += 2;
       //  
       data.setUint16(offset, sampleBits, true); offset += 2;
       //  
       writeString('data'); offset += 4;
       // ,-44 
       data.setUint32(offset, dataLength, true); offset += 4;

      // 
      if (sampleBits === 8) {
        for (var i = 0; i < bytes.length; i++ , offset++) {
          var s = Math.max(-1, Math.min(1, bytes[i]));
          var val = s < 0 ? s * 0x8000 : s * 0x7FFF;
          val = parseInt(255 / (65535 / (val + 32768)));
          data.setInt8(offset, val, true);
        }
      } else {
        for (var index = 0; index < bytes.length; index++ , offset += 2) {
          var sIndex = Math.max(-1, Math.min(1, bytes[index
          ]));
          data.setInt16(offset, sIndex < 0 ? sIndex * 0x8000 : sIndex * 0x7FFF, true);
        }
      }
     return new Blob([data], { type: 'audio/wav' });
    }
  }

  // 
  this.start = function () {
    audioInput.connect(recorder)
    recorder.connect(context.destination)
  }

  // 
  this.stop = function () {
    recorder.disconnect()
  }
  //
  this.getBlob = function () {
    this.stop();
    return audioData.encodeWAV();
  }
  //base64
  this.getBase= function (callback) {
    this.stop();
    audioData.getbase64(res=>{
      callback( res)
    })
  }

  //
  this.play = function (audio) {
  // saveAs(blob, "F:/3.wav");
    audio.src = window.URL.createObjectURL(this.getBlob());
  }

  //
  this.upload = function () {
    return this.getBlob()
  }
  // 
  recorder.onaudioprocess = function (e) {
    audioData.input(e.inputBuffer.getChannelData(0))
    // record(e.inputBuffer.getChannelData(0));
  }
}
// 
HZRecorder.prototype.throwError = function (message) {
  alert(message)
  throw new function () {
    this.toString = function () {
      return message
    }
  }()
}
// 
// HZRecorder.prototype.canRecording = navigator.getUserMedia != null
// 
HZRecorder.prototype.get = function (callback, config) {
  var that = this
  if (callback) {
    if (navigator.getUserMedia) {
      navigator.getUserMedia(
        { audio: true }, // 
        function (stream) {
          var rec = new HZRecorderInit(stream, config)
          callback(rec)
        },
        function (error) {
          switch (error.code || error.name) {
            case 'PERMISSION_DENIED':
            case 'PermissionDeniedError':
              that.throwError('')
              break
            case 'NOT_SUPPORTED_ERROR':
            case 'NotSupportedError':
              that.throwError('')
              break
            case 'MANDATORY_UNSATISFIED_ERROR':
            case 'MandatoryUnsatisfiedError':
              that.throwError('')
              break
            default:
              that.throwError(
                ':' + (error.code || error.name)
              )
              break
          }
        }
      )
    } else {
      that.throwErr('')
    }
  }
}
let obj = new HZRecorder()
export default obj
